@use "../abstracts/variables";

.trip-card {
  border: 1px solid variables.$call-to-action-light;
  margin: 1rem;
  padding: 1.6rem;
  max-width: 40rem;
  // min-width: 30rem;
  border-radius: 10px;

  &__counter,
  &__type {
    font-weight: 300;
    font-size: 2rem;
  }

  &__row {
    display: flex;
    width: 100%;
    margin-bottom: 2rem;
    position: relative;

    &__group {
      position: relative;
      color: red;

      // &:not(:first-child) {
      // 	margin-left: 2rem;
      // }
    }

    &.title_row {
      justify-content: space-between;
    }

    &.date-row {
      width: 100%;
      display: grid;
      grid-template-columns: 50% 40% 10%;

      & > *:not(.round-button) {
        margin-right: 1rem;
      }

      & input {
        width: 100%;
        margin-right: 1.3rem;
      }

      &.round-button {
        width: 100%;
      }
    }

    &.date-row,
    &.expense-row {
      margin-bottom: 1rem;
    }
  }

  &__date {
    margin-bottom: 4rem;
  }

  &__expense-buttons {
    display: flex;
    justify-self: start;
  }
}

.expense-row {
  width: 100%;
  display: grid;
  grid-template-columns: 5% 8rem 2fr 1fr;
  justify-items: center;

  & .toggle-switch {
    grid-column: 2/3;
    align-self: center;
  }

  & > .cost-inputs {
    grid-column: 3/4;
    max-width: 100%;
    display: flex;

    & input {
      padding-left: 2rem;
      width: 100%;
    }

    & label {
      left: 12.9%;
    }

    & .dollar-sign {
      top: 50%;
      left: 5%;
      z-index: 1;
      font-size: 1.8rem;
      transform: translateY(-50%);
      position: absolute;
      color: variables.$text-color;
    }

    & select {
      border: solid 1px variables.$call-to-action;
      border-radius: variables.$radius;
      color: variables.$text-color;
      outline: none;
      padding: 0.75rem;
      background: none;
      margin-right: 1rem;
      width: 35%;

      &:hover {
        border-color: variables.$call-to-action-light;
      }

      &:focus {
        border-color: variables.$call-to-action-med;
      }
    }
  }

  & .trip-card__expense-buttons {
    grid-column: 4/5;
    padding: 0.2rem;
    justify-content: start;

    & > * {
      margin: 0 0.3rem;
    }
  }
}

.trip-creation-modal {
  display: none;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100vh;
  width: 100vw;

  background: rgba(0, 0, 0, 0.459);

  & .inner-modal {
    position: absolute;
    height: 75%;
    width: 40%;
    min-width: 30rem;
    max-width: 48rem;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: variables.$radius;

    background-color: variables.$background-color;
    overflow: hidden;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    & > * {
      margin-bottom: 1rem;
    }

    & input {
      width: 25ch;
    }

    &:before {
      position: absolute;
      top: 0;
      content: "";
      background-color: variables.$text-color;
      width: 100%;
      height: 3rem;
    }
  }
}
