@use "../abstracts/variables";

.form {
  &__input {
    font-family: inherit;
    font-size: inherit;

    border: solid 1px variables.$call-to-action;
    border-radius: variables.$radius;
    color: variables.$text-color;
    outline: none;
    padding: 0.75rem;
    background: none;
    min-height: 21px;
    min-width: 9rem;

    &:hover {
      border-color: variables.$call-to-action-light;
    }

    &:focus {
      border-color: variables.$call-to-action-med;
    }

    &::placeholder {
      color: red;
    }
  }

  &__label {
    position: absolute;
    left: 5%;
    top: 50%;
    transform: translateY(-50%);
    padding: 0 0.5rem;
    background-color: variables.$background-color;
    color: variables.$text-color-not-in-use;
    cursor: text;
    -webkit-transition: top 0.1s ease-in, left 0.1s ease-in,
      font-size 0.1s ease-in, color 0.1s ease-in;
    transition: top 0.1s ease-in, left 0.1s ease-in, font-size 0.1s ease-in,
      color 0.1s ease-in;
  }
}

.form__input:focus ~ .form__label,
.form__input:not(:placeholder-shown).form__input:not(:focus) ~ .form__label {
  top: 0;
  font-size: 1.3rem;
  left: 0.8rem;
  color: variables.$call-to-action-light;
}

