@use "../abstracts/variables";

.round-button {
	display: grid;
	place-items: center;
	height: 3rem;
	width: 3rem;
	background: linear-gradient(
		180deg,
		rgba(119, 156, 119, 1) 0%,
		rgba(96, 129, 96, 1) 100%
	);
	align-self: center;
	border-radius: 50%;
	// margin: 0 0.5rem;
	box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.3);
	transition: all 0.2s ease;

	&:hover {
		transform: scale(1.035);
	}

	&:active {
		transform: scale(0.98);
	}

	& svg {
		height: 55%;
		width: 55%;
		& path {
			fill: variables.$call-to-action;
		}
	}
}

.button {
	box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.3);
	background: linear-gradient(
		180deg,
		rgba(119, 156, 119, 1) 0%,
		rgba(96, 129, 96, 1) 100%
	);
	color: variables.$text-color;

	border-radius: variables.$radius;

	border: none;

	&:hover {
		transform: scale(1.01);
	}

	&:active {
		transform: scale(0.99);
	}
}
